#App{
  background-color: rgb(0, 0, 0);
  /* background-color: rgb(1, 1, 36); */
  height: 100vh;
  width: 100%;
  cursor: none;
}

#App::-webkit-scrollbar{
  display: none;
  scrollbar-width: none;
}

.innerContainer{
  display: flex;
  align-items: center;
  justify-content: center;
}

#homeLink{
  text-decoration: none;
  color: white;
  padding-left: 2rem;
}

ul{
  list-style: none;
}



#about, #heading{
  color: white;
}

#heading{
  font-size: 40px;
  padding-top: 4rem;
}

#about{
  font-size: 20px;
  margin: 1.25rem 0rem;
}

.text-content, .image-section{
  padding: 5rem;
  width: 50%;
  display: flex;
  flex-direction: column;
}

#whitelist-form{
  background-color: rgb(98, 0, 255);
  width: 50%;
  padding: 1rem;
  margin-left: 1rem;
}

.image-section video{
    height: 400px;
  }

.whitelistBtn{
  background-color:"black"; 
  color:"white"; border-radius:"0px"; 
  padding:"1rem";
}


#mint-limit{
  color: white;
  background-color: rgb(98, 0, 255);
  padding: 1.22rem;
  margin-left: 17px;
}


@media screen and (max-width: 800px) {
  .innerContainer{
    flex-direction: column;
  }
  .text-content, .image-section{
    width: 100%;
    padding: 1.5rem;
  }
  #heading{
    font-size: 30px;
  }
  #whitelist-form{
    width: 90%;
    padding: 10px;
  }


  #about{
    font-size: 15px;
  }
  .image-section video{
    height: 250px;
  }

}